import React from 'react'; 
import { makeStyles, useTheme } from '@material-ui/core';
import { Button, Container, Grid, Paper, Avatar, Typography } from '@material-ui/core';
import {scrollTo} from './utils/helpers'

import LaptopMacIcon from '@material-ui/icons/LaptopMac';

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      display: "flex",
      flexDirection: "column",
      color: theme.palette.primary.main,
      height: "102vh"
    },
    particles: {
        height: "100vh"
    },
    textBox: {
        // margin: "10vh 0 0 10vh",
        display: "flex",
        flexDirection:"column",
        justifyContent: "center",
        marginLeft: "auto",
        marginRight: "auto",
        marginTop: "25vh"
    },
    container: {
        display: "flex",
        justifyContent: "center",
        marginTop: "auto",
        marginBottom: "15vh"
      },

  }));

const bigTextStyle = {        
    fontSize: "5vh",
    fontWeight: "bold",
    fontFamily:"Roboto",
    margin:"0",
    textAlign: "center"
}
const subtitleStyle = {        
    fontSize: "4vh",
    fontWeight: "lighter",
    fontFamily:"Roboto",
    margin:"0",
    textAlign: "center"
}

function LandingPage(props) {
    const theme = useTheme();
    const classes = useStyles(theme);

    return (
        <div className={classes.root}>

            <div className={classes.textBox}>
                 <p style={bigTextStyle}>Big Text.</p>
                 <p style={subtitleStyle}>Some subtitle text.</p>
            </div>

            <Grid container spacing={3} justifyContent="center">
                <Grid item xs={3}>
                    <div style={{"flexDirection":"column", "justifyContent":"center", "display":"flex"}}>
                        {/* <Avatar>
                            <LaptopMacIcon/>
                        </Avatar>
                        <Typography variant="h3">Prout</Typography> */}
                    </div>
                </Grid>
                <Grid item xs={3}>
                    <Paper/>    
                </Grid>
                <Grid item xs={3}>
                    <Paper/>
                </Grid>

            </Grid>


            <Container className={classes.container}>
                <Button onClick={() => scrollTo(props.buttonRef)} variant="contained" color="primary" size="large">
                Learn More
                </Button>
            </Container>
        </div>
      );

}

export default LandingPage;